$pColor:#f67c92;
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-behavior:smooth ;
  font-family: 'Poppins', sans-serif;
}
#home{
  height: 100vh;
  width: 100%;
  background: url(../assets/background.png);
  background-position: center;
  background-size: cover;
}
nav{
  transition: 0.5s;
  width: 200px;
  height: 100vh;
  background-color: #f67c92;
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 0%;
  right: -200px;
  a{
    color: white;
    padding: 0 15px;
    font-size: 1rem;
    text-decoration: none;
    margin: 20px 0;
    transition: 0.5s;
    &:hover{
      color: wheat;
    }
  }
}
#header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 95%;
  margin: 0 auto;
  padding: 10px 0;
  #logo{
    width: 100px;
    height: 70px;
  }
}
#sidebar{
  width: 80px;
  height: 100vh;
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  img{
    margin: 10px 0;
    width: 20px;
  }
}
.text{
  width: 280px;
  h2{
    font-weight: 600;
  }
  p{
    font-size: 13px;
    color: rgba(0, 0, 0, 0.459);
  }
  .box{
    background-color: $pColor;
    height: 12px; width: 12px;
    margin: 20px 0;
  }
  .line-1{
    width: 80px;
    height: 3px;
    background-color: $pColor;
  }
  .line-2{
    width: 60px;
    height: 3px;
    background-color: $pColor;
    margin: 10px 0;
  }
}
.btn{
  cursor: pointer;
    padding: 7px 15px;
    border: none;
    outline: none;
    border: 2px solid $pColor;
    background-color: white;
    transition: 0.5s;
    margin: 10px 0;
    &:hover{
      background-color: $pColor;
    }
}
.h-content{
  margin-left: 150px;
  margin-top: 50px;
}
#about{
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  img{
    width: 700px;
  }
}
.about-text{
  width: 450px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  h2,h3,p{
    text-align: right;
  }
  h3{
    color: rgba(0, 0, 0, 0.418);
  }
}
#features{
  height: 70vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.f-cards{
  width: 900px;
  display: flex;
  justify-content: space-between;
}
.card{
  width: 250px;
  text-align: center;
  img{
    width: 100px;
  }
  h3{
    font-weight: 600;
  }
  p{
    font-size: 11px;
    color: rgba(0, 0, 0, 0.459);
  }
}
.box{
  background-color: $pColor;
  height: 12px; width: 12px;
  margin: 20px 0;
}
.line-1{
  width: 80px;
  height: 3px;
  background-color: $pColor;
}
.line-2{
  width: 60px;
  height: 3px;
  background-color: $pColor;
  margin: 10px 0;
}
.f-button{
  display: flex;
  gap: 20px;
  margin: 20px 0;
  .lines-box{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    .box{
      margin: 10px 0;
    }
  }
}

#courses{
  width: 80%;
  margin: 0 auto;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  img{
    width: 600px;
  }
  }
#offers{
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  img{
    width: 600px;
  }
  .of-content{
    margin-right: 4rem;
  }
  }
  #contact{
    width: 85%;
    height: 100vh;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;

  .c-content{
    .form{
      width: 300px;
      display: flex;
      flex-direction: column;
      input{
        outline: none;
        border: 2px solid $pColor;
        margin: 5px 0;
        padding: 10px;
      }
      .buttons{
        display: flex;
        justify-content: space-between;
        button{
          width: 47%;
        }
      }
    }
  }
  img{
    width: 500px;
  }

}
#footer{
  width: 80%;
  margin: 0 auto;
}
.f-content{
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .f-text{
    display: flex;
    gap: 30px;
    .txt-box{
      div{
        font-size: 20px;
        color: $pColor;
        font-weight: 400;
        margin: 10px 0;
      }
      p{
        color: rgba(0, 0, 0, 0.4);
        font-size: 13px;
      }
    }
  }
  .f-logo{
    display: flex;
    flex-direction: column;
    img{
      width: 120px;
    }
    .btn{
      text-align: center;
    }
  }
}
.cp-msg{
  background-color: $pColor;
  text-align: center;
  padding: 10px;
}

.bars{
  width: 50px;
  position: fixed;
  top: 5%;
  right: 3%;
  z-index: 2;
}